/* c-section
--------------------------- */
@use "./../../foundation/system" as s;

// component section
.c-section {
    // renewal0
    font-size: 16px;
    p {
        font-size: inherit;
    }
    // renewal0
    background-color: s.$c-white;
    &--primary {
        @extend .c-section;
        background-color: s.$c-primary;
    }
    &--primaryLight {
        @extend .c-section;
        background-color: s.$c-primaryLight;
    }
    &--lightGray {
        @extend .c-section;
        background-color: s.$c-lightGray;
    }

    &__inner {
        @include s.content-maxwidth();
        margin: 0 auto;
        // padding: 10px 20px;
        padding: 120px 20px;
    }
    // 命名規則…
    &__step-inner1 {
        @include s.content-maxwidth();
        margin: 0 auto;
        padding: 120px 20px 20px;
    }
    &__step-inner2 {
        background-color: s.$c-lightGray;
        margin: 0 calc(50% - 50vw);
        padding: 32px calc(50vw - 50% + 8px) 120px;
        width: 100vw;

        position: relative;
        &:before {
            content: "";
            position: absolute;
            top: 0;
            right: 0;
            left: 0;
            width: 0;
            margin: auto;
            border-top: 40px solid s.$c-white;
            border-bottom: 40px solid transparent;
            border-right: 100px solid transparent;
            border-left: 100px solid transparent;
        }
    }
    &__step-inner3 {
        background-color: s.$c-primaryLight;
        margin: 0 calc(50% - 50vw);
        padding: 32px calc(50vw - 50% + 8px) 120px;
        width: 100vw;

        position: relative;
        &:before {
            content: "";
            position: absolute;
            top: 0;
            right: 0;
            left: 0;
            width: 0;
            margin: auto;
            border-top: 40px solid s.$c-white;
            border-bottom: 40px solid transparent;
            border-right: 100px solid transparent;
            border-left: 100px solid transparent;
        }
    }

    &__block {
        margin: 32px auto 0px;
    }
    &__paragraph {
        display: table;
        text-align: left;
        max-width: 1000px;
        margin: 0 auto;
    }
}

.c-column {
    display: grid;
    // grid-template-columns: repeat(auto-fit, 300px);
    // grid-gap: 20px 20px;

    // 左寄せ
    justify-content: center;

    // 中央寄せ
    // width: 90%;
    // margin: 0 auto;

    &--one {
        @extend .c-column;
        grid-template-columns: repeat(auto-fit, 940px);
        grid-gap: 20px 20px;
    }
    &--two {
        @extend .c-column;
        grid-template-columns: repeat(auto-fit, 460px);
        grid-gap: 20px 20px;
    }
    &--three {
        @extend .c-column;
        grid-template-columns: repeat(auto-fit, 300px);
        grid-gap: 20px 20px;
    }
    &--five {
        @extend .c-column;
        grid-template-columns: repeat(auto-fit, 200px);
        grid-gap: 16px 16px;
    }
    @include s.breakpoint(tablet) {
        grid-template-columns: repeat(auto-fit, 100%);
        grid-gap: 16px 16px;
    }
    @include s.breakpoint(sp) {
    }
}

.c-card1 {
    background-color: s.$c-white;
    border: solid 1px s.$c-lightGray;
    border-radius: 4px;
    padding: 24px;
    &__description {
        padding: 20px 0 0;
        p + p {
            margin-top: 8px;
        }
    }
}
.c-card2 {
    background-color: s.$c-white;
    border: solid 2px s.$c-lightGray;
    border-radius: 4px;
    padding: 24px;
    &__list {
        text-align: left;
        margin-top: 8px;
    }
}
.c-card3 {
    background-color: s.$c-white;
    border: solid 1px s.$c-lightGray;
    border-radius: 4px;
    padding: 24px;
    img {
        margin: 18px 0;
    }
}
.c-card4 {
    background-color: s.$c-white;
    padding: 16px;
    // text-align: center;

    &__image {
        margin: 0 auto;
    }
    &__description {
        margin: 8px 0;
        // text-align: left;
    }
    &__button {
        display: inline-flex;
        justify-content: center;
        align-items: center;
        font-size: 18px;
        font-weight: bold;
        color: s.$c-primary;
        background-color: s.$c-white;
        border: solid 2px s.$c-primary;
        border-radius: 2px;
        padding: 8px 14px;
        width: 100%;
        text-decoration: none;
        &:hover {
            color: s.$c-white;
            background-color: s.$c-primary;
        }
    }
}
.c-card5 {
    border: solid 1px s.$c-lightGray;
    border-radius: 8px;
    &__title {
        text-align: center;
        font-size: 18px;
        font-weight: bold;
        color: s.$c-white;
        background-color: s.$c-primary;
        border-radius: 8px 8px 0 0;
        padding: 8px;
    }
    &__content {
        display: flex;
        flex-direction: column;
        gap: 12px;

        padding: 24px;
    }
    &__picture {
        img {
            margin: 0 auto;
        }
    }
}

.c-box1 {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    max-width: 800px;
    margin: 0 auto;
    padding: 20px 8px 36px;
    background-color: s.$c-white;
    border-radius: 16px;
    font-size: 24px;
    font-weight: bold;

    &__description {
        margin-top: 16px;
    }
    &__number {
        font-size: 64px;
    }
    @include s.breakpoint(tablet) {
        font-size: 20px;
        text-align: center;
    }
    @include s.breakpoint(sp) {
    }
}
.c-box2 {
    max-width: 800px;
    margin: 0 auto;
    padding: 36px 8px;
    background-color: s.$c-white;
    border: solid 4px s.$c-primary;
    border-radius: 2px;
    &__figure {
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 16px;
        h3 {
            text-align: left;
        }
    }
    &__description {
        max-width: 640px;
        margin: 16px auto 0;
    }
    @include s.breakpoint(tablet) {
        &__figure {
            flex-wrap: wrap;
            flex-direction: column-reverse;
            h3 {
                text-align: center;
            }
        }
    }
    @include s.breakpoint(sp) {
    }
}

.c-dl1 {
    display: flex;
    justify-content: center;
    align-items: flex-end;
    gap: 24px;

    &__title {
        font-size: 24px;
        font-weight: bold;
        span {
            font-size: 56px;
        }
    }
    &__description {
        display: flex;
        flex-direction: column;
        gap: 8px;
    }
    &__chat {
        display: flex;
        justify-content: center;
        align-items: flex-end;
        gap: 16px;
    }
    &__call {
        display: flex;
        justify-content: center;
        align-items: flex-end;
        gap: 16px;

        span {
            &:nth-of-type(1) {
                font-size: 40px;
                font-weight: bold;
                line-height: 1.1;
            }
            &:nth-of-type(2) {
                font-size: 18px;
            }
            &:nth-of-type(3) {
                font-size: 14px;
            }
        }
    }
    @include s.breakpoint(tablet) {
        &__description {
            display: none;
        }
    }
    @include s.breakpoint(sp) {
    }
}

.c-figure1 {
    max-width: 1000px;
    margin: 0 auto;

    &__picture {
        display: block;
        max-width: 550px;
        margin: 0 auto;
    }
    &__figcaption {
        margin-top: 20px;
    }
}
.c-figure2 {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    gap: 16px;
    &--reverse {
        @extend .c-figure2;
        flex-direction: row-reverse;
    }

    &__picture {
        // width: 360px;
        // flex: 0.5;
        max-width: 100%;
    }
    &__figcaption {
        flex: 1;
        max-width: 100%;
        h3 {
            text-align: left;
        }
        p {
            margin-top: 16px;
        }
    }
    @include s.breakpoint(tablet) {
        &__picture {
            width: 100%;
            img {
                margin: 0 auto;
            }
        }
    }
    @include s.breakpoint(sp) {
    }
}

.c-lists1 {
    max-width: 1000px;
    margin: 0 auto;
    border-top: solid 1px s.$c-lightGray;
    &__list {
        border-bottom: solid 1px s.$c-lightGray;
        padding: 20px 16px;
    }
}

.c-chat1 {
    padding: 40px 24px;
    background-color: s.$c-white;

    &__tag {
        text-align: center;
        span {
            color: s.$c-white;
            background-color: s.$c-primary;
            padding: 4px 8px;
            margin: 8px 0 0;
            border-radius: 4px;
            display: inline-flex;
            justify-content: center;
            align-items: center;
        }
    }
    &__figure {
        display: flex;
        align-items: center;
        gap: 16px;
        margin-top: 24px;
        &:nth-of-type(even) {
            flex-direction: row-reverse;
            .c-chat1__figcaption:before {
                left: 100%;
                border-right: 0;
                border-left: 15px solid s.$c-primaryLight;
            }
        }
    }
    &__picture {
        width: 120px;
        img {
            border-radius: 50%;
        }
    }
    &__figcaption {
        background-color: s.$c-primaryLight;
        border-radius: 4px;
        padding: 16px;
        width: calc(100% - 120px * 2 - 16px * 2);

        position: relative;
        &:before {
            content: "";
            position: absolute;
            top: 50%;
            left: -30px;
            margin-top: -15px;
            border: 15px solid transparent;
            border-right: 15px solid s.$c-primaryLight;
        }

        p + p {
            margin-top: 14px;
        }
    }

    @include s.breakpoint(tablet) {
        &__figure {
            align-items: flex-start;
        }
        &__figcaption {
            width: 100%;
            &:before {
                top: 40px;
            }
        }
    }
    @include s.breakpoint(sp) {
    }
}

.c-video {
    text-align: center;
    &__iframe {
        width: 640px;
        height: 360px;
        max-width: 100%;
    }
}
