/* l-footer
--------------------------- */
@use "./../foundation/system" as *;

.l-footer {
    font-size: 14px;
    background: $c-black;
    padding: 40px 16px;

    &__inner {
        @include content-maxwidth(1000px);
        margin: 0 auto;
        padding: 20px 10px;
        color: $c-white;
        a {
            color: $c-white;
        }

        display: flex;
        justify-content: space-between;
        align-items: flex-start;
    }

    &__title {
        max-width: 1000px;
        margin: 0 auto;
        padding: 20px 10px 0px;
        a {
            color: #fff;
            font-size: 18px;
            font-weight: bold;
            text-decoration: none;
        }
    }
    // &__nav {
    // }
    &__navtitle {
        color: $c-white;
        font-size: 16px;
        font-weight: bold;
        margin-bottom: 8px;
    }
    &__navlists {
        display: flex;
        flex-direction: column;
        // flex-wrap: wrap;
        gap: 4px;
        // max-height: 320px;
    }

    &--info {
        @extend .l-footer;
        background-color: $c-white;
        text-align: center;
        // padding: 40px 0;
    }
    &__logo {
        margin: 18px auto;
    }
    &__copy {
        font-size: 12px;
    }

    @include breakpoint(tablet) {
    }
    @include breakpoint(sp) {
        &__inner {
            flex-direction: column;
            gap: 20px;
        }
    }
}
