/* c-text
--------------------------- */
@use "./../../foundation/system" as *;

.c-text-heading1 {
    font-size: 40px;
    font-weight: bold;
}
.c-text-heading2 {
    text-align: center;
    font-size: 32px;
    font-weight: 900;
}
.c-text-heading3 {
    text-align: center;
    font-size: 24px;
    font-weight: 900;
}
.c-text-heading4 {
    text-align: center;
    font-size: 20px;
    font-weight: 900;
}

