.c-text-paragraph {
    font-size: 16px;
    font-weight: normal;
}

.c-text-white {
    color: $c-white;
}
.c-text-primary {
    color: $c-primary;
}

.c-text-marker {
    background: linear-gradient(transparent 80%, $c-primary 20%);
}

.c-text-bold {
    font-weight: bold;
}

.c-text-center {
    text-align: center;
}
