/* system
--------------------------- */

// color system
// ---------------------
$c-black: #333;
$c-white: #fff;

$c-darkGray: #999;
$c-lightGray: #f0f0f0;

$c-primary: #ff9933;
$c-primaryLight: #fff8e5;

$c-primary3: #337ab7;

$c-secondary: #ff2929;

// typography
// ---------------------

// border radius/solid
// ---------------------

// box-shadow
// ---------------------

// transition
// ---------------------
@mixin transition-base($second: 0.4s) {
    transition: all $second;
}

// content max-width
// ---------------------
@mixin content-maxwidth($px: 1200px) {
    max-width: $px;
}

// font system
// ---------------------
// @mixin garamond($weight: bold) {
//     font-family: "Cormorant Garamond", serif;
//     font-weight: $weight;
// }

// Google Font Awesome
// ---------------------
@import url(https://fonts.googleapis.com/icon?family=Material+Icons);

// responsive system
// ---------------------
@mixin breakpoint($point) {
    @if $point == pc {
        @media (min-width: 960px) {
            @content;
        }
    }
    @if $point == tablet {
        @media (max-width: 960px) {
            @content;
        }
    }
    @if $point == sp {
        @media (max-width: 380px) {
            @content;
        }
    }
}

.pc-only {
    display: inline-block;
}
.sp-only {
    display: none;
}
@include breakpoint(tablet) {
}
@include breakpoint(sp) {
    .pc-only {
        display: none;
    }
    .sp-only {
        display: inline-block;
    }
}
