@use "./../../foundation/system" as *;

// ──────────────────────────────
// .l-section
// ──────────────────────────────
.l-section {
    padding: 40px 0 0;
    &__inner {
        @include content-maxwidth();
        margin: 0 auto;
        padding: 0 16px 40px;
    }
}

// ──────────────────────────────
// .p-home-heading2
// ──────────────────────────────
.p-home-heading2 {
    text-align: center;
    font-size: 28px;
    font-weight: bold;
    color: $c-white;
    background-color: $c-primary;
    padding: 16px;
}
.p-home-heading2--v2 {
    text-align: center;
    font-size: 28px;
    font-weight: bold;
    padding: 16px 8px;
    border-top: solid 4px $c-primary;
    border-bottom: solid 4px $c-primary;
}

// ──────────────────────────────
// .p-home-description
// ──────────────────────────────
.p-home-description {
    p {
        font-size: 16px;
    }
}

// ──────────────────────────────
// .p-home-fv
// ──────────────────────────────
.p-home-fv {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    gap: 56px;

    &__figcaption {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 16px;
        text-align: center;
    }
    &__title {
        font-size: 24px;
        font-weight: bold;
    }
    &__picture {
        max-width: 350px;
    }
}

// ──────────────────────────────
// .p-home-banner
// ──────────────────────────────
.p-home-banner {
    display: flex;
    justify-content: center;
    align-items: center;
    max-width: 600px;
    margin: 0 auto;
    // &__picture {
    // }
}

// ──────────────────────────────
// .p-home-reason
// ──────────────────────────────
.p-home-reason {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    gap: 16px;
    margin: 40px 8px;
    &--reverse {
        @extend .p-home-reason;
        flex-direction: row-reverse;
    }
    // &:nth-of-type(odd) {
    //     flex-direction: row-reverse;
    // }
    p {
        font-size: 16px;
    }
    &__figcaption {
        max-width: 800px;
        h3 {
            font-size: 24px;
            font-weight: bold;
        }
    }
}

// ──────────────────────────────
// .p-home-video
// ──────────────────────────────
.p-home-video {
    text-align: center;
    &__iframe {
        width: 640px;
        height: 360px;
        max-width: 100%;
    }
}

// ──────────────────────────────
// .p-home-service
// ──────────────────────────────
.p-home-service {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    gap: 14px;
    max-width: 1000px;
    margin: 0 auto;

    &__list {
        font-size: 16px;
        text-align: center;
        padding: 32px 20px;
        border: solid 1px $c-darkGray;
        border-radius: 4px;
        width: 33%;
        max-width: 320px;
        &:nth-of-type(4),
        &:nth-of-type(5) {
            max-width: none;
            width: 49%;
        }
        img {
            margin: 0 auto;
        }
        h3 {
            font-size: 20px;
            margin: 8px 0;
        }
    }
    @media screen and (max-width: 568px) {
        justify-content: center;
        &__list {
            width: 100%;
            &:nth-of-type(4),
            &:nth-of-type(5) {
                max-width: 320px;
                width: 100%;
            }
        }
    }
}

// ──────────────────────────────
// .p-home-feature
// ──────────────────────────────
.p-home-feature {
    font-size: 16px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: stretch;
    gap: 12px;
    max-width: 1000px;
    margin: 40px auto;
    &__list {
        text-align: center;

        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;
        gap: 8px;
        width: 100%;
        max-width: 240px;
        padding: 32px 16px;
        border: solid 1px $c-darkGray;
        border-radius: 8px;
        h3 {
            font-size: 18px;
        }
        p {
            font-size: 16px;
        }
    }
    img {
        margin: 0 auto;
    }
}

// ──────────────────────────────
// .p-home-flow
// ──────────────────────────────
.p-home-flow {
    font-size: 16px;

    max-width: 1000px;
    margin: 20px auto;
    padding: 16px 24px;
    border: solid 2px $c-darkGray;
    border-radius: 8px;
    &__summary {
        font-weight: bold;
        position: relative;
        list-style: none;
        &:hover {
            cursor: pointer;
        }
        span {
            color: $c-primary;
            margin-right: 16px;
        }
        &::-webkit-details-marker,
        &::marker {
            display: none;
        }
        &:after {
            position: absolute;
            top: -10px;
            right: 0;
            content: "\e145";
            font-family: "Material Icons";
            font-size: 28px;
            color: rgba($color: $c-primary, $alpha: 1);
            margin: auto;
            transition: 0.4s;
        }
    }
    &__content {
        padding-top: 8px;
    }
    &[open] {
        summary {
            &:after {
                // transform: rotate(135deg); /* アイコンを回転 */
                content: "\e15b";
            }
        }
    }
}

// ──────────────────────────────
// .p-home-faq
// ──────────────────────────────
.p-home-faq {
    font-size: 16px;
    padding: 16px 8px;
    max-width: 1000px;
    margin: 20px auto;
    &__title {
        color: $c-primary;
        font-size: 20px;
        font-weight: bold;
        padding: 8px 16px;
        border-top: solid 1px $c-darkGray;
        border-bottom: solid 1px $c-darkGray;
    }
    &__lists {
        margin: 24px 0;
        li {
            position: relative;
            padding-left: 24px;
            margin: 8px;
        }
        li:nth-of-type(1) {
            font-weight: bold;
        }
        li:before {
            content: "Q";
            left: 0;
            position: absolute;
            font-weight: bold;
            color: $c-primary;
        }
        li:nth-of-type(2):before {
            content: "A";
        }
    }
}

// ──────────────────────────────
// .p-home-link
// ──────────────────────────────
.p-home-link {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: stretch;
    gap: 16px;
    margin: 20px 0;

    &__list {
        text-align: center;
        font-size: 18px;

        width: 100%;
        max-width: 240px;
        img {
            margin: 0 auto 16px;
        }
    }
    &__link {
        width: 100%;
        height: 100%;
        display: inline-block;
        padding: 40px 20px;
        color: $c-black;
        border: solid 2px $c-primary;
        border-radius: 8px;
        text-decoration: none;
        transition: 0.4s;
        &:hover {
            color: $c-white;
            background-color: $c-primary;
        }
    }
}

// ──────────────────────────────
// .p-home-case
// ──────────────────────────────
.p-home-case {
    font-size: 16px;

    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: stretch;
    gap: 16px;
    margin: 20px auto;
    &__list {
        max-width: 320px;
        border: solid 2px $c-darkGray;
        border-radius: 8px;
    }
    &__content {
        padding: 16px;
        p {
            font-size: inherit;
        }
        h3 {
            font-weight: bold;
            margin: 8px 0;
        }
        a {
            display: inline-block;
            width: 100%;
            text-align: center;
        }
    }
}

// ──────────────────────────────
// .p-home-seminar
// ──────────────────────────────
.p-home-seminar {
    font-size: 16px;

    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: stretch;
    gap: 20px;
    margin: 20px auto;
    max-width: 1000px;
    &__list {
        max-width: 440px;
    }

    &__figure {
        display: flex;
        gap: 8px;
        padding: 8px;
        border: solid 2px $c-darkGray;
        border-radius: 8px;
        margin: 0 0 8px;
        h3 {
            font-size: 18px;
            margin: 0 0 16px;
        }
    }
}

// ──────────────────────────────
// .p-home-knowledge
// ──────────────────────────────
.p-home-knowledge {
    font-size: 18px;

    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    gap: 20px;
    max-width: 1000px;
    margin: 20px auto;
    &__list {
        width: 440px;
        max-width: 100%;
        a {
            color: $c-black;
            text-decoration: none;

            width: 100%;
            display: flex;
            // justify-content: center;
            align-items: center;
            gap: 16px;
            padding: 20px 32px;
            border: solid 2px $c-primary;
            border-radius: 8px;
        }
    }
}

// ──────────────────────────────
// .p-home-call
// ──────────────────────────────
.p-home-call {
    text-align: center;

    max-width: 800px;
    margin: 20px auto;
    padding: 20px 8px;
    border: solid 2px $c-darkGray;
    border-radius: 8px;

    p {
        text-align: center;
    }
    &__copy {
        font-size: 18px;
        position: relative;
        display: inline-block;
        padding: 0 40px;
        &:before,
        &:after {
            content: "";
            position: absolute;
            top: 50%;
            display: inline-block;
            width: 32px;
            height: 1px;
            background-color: black;
        }
        &:before {
            transform: rotate(60deg);
            left: 0;
        }
        &:after {
            transform: rotate(-60deg);
            right: 0;
        }
    }
    &__tell {
        color: $c-black;
        text-decoration: none;
        font-size: 32px;
        font-weight: bold;

        display: inline-block;
        margin: 8px 0 0;
    }
    &__annotation {
        font-size: 16px;
    }
    @include breakpoint(tablet) {
        &__copy {
            font-size: 14px;
        }
    }
    @include breakpoint(sp) {
    }
}

// ──────────────────────────────
.top-content__body {
    width: 840px;
    margin: 0 auto;
    float: none;
}
.top-content__secondary-phone-number {
    line-height: 2;
}
.top-content__body--sp {
    color: $c-white;
    font-size: 16px;
    padding: 20px 8px;
    p {
        text-align: center;
        &:nth-of-type(2) {
            font-size: 32px;
        }
    }
    @media screen and (min-width: 568px) {
        display: none;
    }
}

// ──────────────────────────────
// .p-page-heading
// ──────────────────────────────
.p-page-heading1 {
    color: $c-primary;
    font-size: 24px;
    font-weight: bold;
    text-align: center;
}
.p-page-heading2 {
    font-size: 20px;
    font-weight: bold;
    text-align: center;
}

// ──────────────────────────────
// .p-page-links
// ──────────────────────────────
.p-page-links {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: stretch;
    gap: 16px;

    &__list {
        max-width: 480px;
    }
    &__link {
        color: $c-black;
        text-decoration: none;

        height: 100%;
        width: 100%;
        padding: 20px 20px;
        border: solid 2px $c-darkGray;
        border-radius: 8px;
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 12px;
        transition: 0.4s;
        &:hover {
            color: $c-black;
            border-color: $c-primary;
        }
    }
    &__content {
        flex: 1;
        h3 {
            font-size: 20px;
            font-weight: bold;
            color: $c-primary;
        }
        // renewal0
        p {
            font-size: 16px;
            margin: 8px 0;
        }
        span {
            font-size: 16px;
            display: block;
            text-decoration: underline;
        }
    }
}

// ──────────────────────────────
// .p-page-links2
// ──────────────────────────────
.p-page-links2 {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    gap: 16px;

    &__list {
        width: 100%;
        max-width: 320px;
    }
    &__link {
        font-size: 16px;
        font-weight: bold;
        text-decoration: none;

        width: 100%;
        padding: 20px 20px;
        border: solid 2px $c-primary;
        border-radius: 2px;
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 12px;
        transition: 0.4s;
        &:hover {
            border-color: $c-darkGray;
        }
    }
}

// ──────────────────────────────
// .p-page-figure
// ──────────────────────────────
.p-page-figure {
    font-size: 16px;

    display: flex;
    flex-direction: row-reverse;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    gap: 16px;
    // &__picture{}
    // &__figcaption{}
    &__links {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        // align-items: stretch;
        gap: 16px;
        max-width: 420px;
    }
    &__link {
        color: $c-black;
        text-decoration: none;

        min-width: 163px;
        padding: 24px 8px;
        border: solid 2px $c-darkGray;
        border-radius: 8px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 4px;
        &:hover {
            color: $c-black;
            border-color: $c-primary;
        }
    }
}

// ──────────────────────────────
// .p-services-heading
// ──────────────────────────────
.p-services-heading2 {
    color: $c-primary;
    font-size: 24px;
    font-weight: bold;
    text-align: center;
}

// ──────────────────────────────
// .p-services-fv
// ──────────────────────────────
.p-services-fv {
    display: flex;
    flex-direction: row-reverse;
    justify-content: center;
    align-items: center;
    gap: 20px;
    // border: solid 1px $c-darkGray;
    // border-radius: 2px;
    max-width: 1000px;
    margin: 40px auto;

    &__picture {
        flex: 1;
        // max-width: 500px;
        position: relative;
        &:after {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-image: linear-gradient(
                270deg,
                transparent 0 60%,
                $c-white 100%
            );
        }
    }
    &__figcaption {
        flex: 1;
        min-width: 340px;

        display: flex;
        flex-direction: column;
        // justify-content: center;
        align-items: center;
        gap: 8px;
        padding: 20px 24px;
    }
    &__subtitle {
        text-align: center;
        font-size: 18px;
    }
    &__title {
        font-size: 32px;
        font-weight: bold;
    }
    &__copy {
        color: #30bd00;
        font-size: 18px;
        font-weight: bold;
    }

    @include breakpoint(tablet) {
        flex-direction: column;
        &__picture {
            &:after {
                background-image: linear-gradient(
                    180deg,
                    transparent 0 60%,
                    $c-white 100%
                );
            }
        }
    }
    @include breakpoint(sp) {
    }
}

// ──────────────────────────────
// .p-services-function
// ──────────────────────────────
.p-services-function {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: stretch;
    gap: 16px;
    padding: 40px 8px;
    background-color: $c-lightGray;
    &__list {
        width: 100%;
        max-width: 320px;
