    }
}

// ──────────────────────────────
// .p-services-lists
// ──────────────────────────────
.p-services-lists {
    margin: 40px auto;
    &__list {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        gap: 16px;
        padding: 20px;
