}

// ──────────────────────────────
// .p-services-links
// ──────────────────────────────
.p-services-links {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;

    max-width: 800px;
    margin: 0 auto;
    &__list {
        width: 100%;
        max-width: 390px;

        display: flex;
        justify-content: center;
        // align-items: center;
        gap: 12px;
    }
    &__content {
        display: flex;
        flex-direction: column;
        gap: 8px;
        flex: 1;
        h3 {
            font-size: 20px;
        }
        p {
            font-size: 16px;
        }
    }
}

// ──────────────────────────────
// .p-services-price
// ──────────────────────────────
.p-services-price {
