}

// ──────────────────────────────
// .p-services-case
// ──────────────────────────────
.p-services-case {
    display: flex;
    flex-direction: column;
    gap: 16px;
    max-width: 800px;
    margin: 0 auto;
    &__list {
        padding: 12px 20px;
        border: solid 1px $c-darkGray;
        border-radius: 4px;
    }
    &__title {
        padding: 0 0 12px;
        margin: 0 0 12px;
        border-bottom: dashed 1px $c-darkGray;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: center;
        h3 {
            font-size: 20px;
        }
        span {
            font-size: 16px;
        }
    }
    &__content {
        display: flex;
        flex-wrap: wrap;
        gap: 20px;
        figcaption {
            min-width: 300px;
            flex: 1;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            gap: 8px;
        }
        p {
            font-size: 16px;
        }
    }
    &__cta {
        font-size: 16px;

        text-align: right;
    }
    &__button {
        color: $c-white;
        font-weight: bold;
        text-decoration: none;

        display: inline-block;
        padding: 8px 24px;
        background-color: $c-primary;
        border-radius: 4px;
        &:hover {
            color: $c-white;
            opacity: 0.7;
        }
    }
}

// ──────────────────────────────
// .p-price-lists
// ──────────────────────────────
.p-price-lists {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: stretch;
    gap: 16px;
    &__list {
        font-size: 16px;

        display: flex;
        flex-direction: column;
        gap: 20px;
        width: 100%;
        max-width: 320px;
        padding: 64px 20px;
        border: solid 1px $c-darkGray;
        border-radius: 8px;
        h3 {
            text-align: center;
            font-size: 20px;
        }
        ul {
            padding-left: 32px;
        }
        li {
            list-style: disc;
        }
        a {
            display: block;
            text-align: center;
        }
    }
    &__price {
        text-align: center;
        span {
            font-size: 24px;
            font-weight: bold;
        }
    }
}

// ──────────────────────────────
// .p-price-box
// ──────────────────────────────
.p-price-box {
    font-size: 16px;

    display: flex;
    flex-direction: column;
    gap: 8px;
    padding: 20px;
    margin: 0 auto;
    max-width: 800px;
    border: solid 1px $c-darkGray;
    border-radius: 8px;

    background-image: url(https://placehold.jp/150x150.png),
        url(https://placehold.jp/150x150.png);
    background-position: bottom left, bottom right;
    background-repeat: no-repeat;
    h3 {
        font-size: 20px;
        font-weight: bold;
        text-align: center;
    }
    ul {
        padding-left: 32px;
        max-width: 440px;
        margin: 8px auto 0;
    }
    li {
        list-style: disc;
        margin: 8px 0;
    }
    @include breakpoint(tablet) {
        background-image: none;
    }
    @include breakpoint(sp) {
    }
}

// ──────────────────────────────
// .p-price-box2
// ──────────────────────────────
.p-price-box2 {
    font-size: 16px;

    max-width: 1000px;
    margin: 0 auto;
    padding: 32px 20px;
    // background-color: rgba($color: $c-primary, $alpha: 0.1);
    background-color: $c-primaryLight;

    &__dl {
        display: flex;
        justify-content: space-between;
        align-items: center;
        max-width: 400px;
        margin: 8px auto;
    }
    &__content {
        max-width: 800px;
        margin: 0 auto;
        padding: 20px 8px;
        background-color: $c-white;
        h3 {
            text-align: center;
            font-size: 20px;
            font-weight: bold;
        }
    }
    &__table {
        table {
            margin: 0 auto;
            border-collapse: collapse;
            border: solid 2px $c-darkGray;
        }
        th,
        td {
            padding: 16px 10px;
        }
        th {
            border: solid 1px $c-darkGray;
            background-color: $c-lightGray;
        }
        td {
            border: dashed 1px $c-darkGray;
        }
        @media only screen and (max-width: 600px) {
            white-space: nowrap;
            overflow-x: auto;
            -webkit-overflow-scrolling: touch;
        }
    }
}

// ──────────────────────────────
// .p-price-table
// ──────────────────────────────
.p-price-table {
    font-size: 16px;

    table {
        border-collapse: collapse;
        border: solid 2px $c-black;
        margin: 0 auto;
    }
    th,
    td {
        padding: 8px 20px;
        vertical-align: middle;
    }
    th {
        color: $c-white;
        border: solid 1px $c-black;
        background-color: $c-darkGray;
    }
    td {
        border: dashed 1px $c-black;
    }
    @media only screen and (max-width: 600px) {
        white-space: nowrap;
        overflow-x: auto;
        -webkit-overflow-scrolling: touch;
    }
}

