/* l-breadcrumb
--------------------------- */
@use "./../foundation/system" as *;

.l-breadcrumb {
    // renewal0
    margin: 160px auto 0;
    font-size: 14px;
    @include content-maxwidth();
    // margin: 8px auto 0;
    padding: 8px 20px;

    li {
        display: inline;
        &:after {
            content: ">";
            padding: 0 0.2em;
            color: $c-darkGray;
        }
        &:last-child:after {
            content: "";
        }
    }
    @include breakpoint(tablet) {
        margin: 64px auto 0;
    }
    @include breakpoint(sp) {
    }
}
