/* c-column
--------------------------- */
@use "./../../foundation/system" as *;

.c-cta {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: flex-end;
    gap: 18px;
    padding-bottom: 6px;

    &__content {
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        align-items: center;
        gap: 4px;
    }

    &__copy {
        font-size: 18px;
        text-align: center;
        position: relative;
        display: inline-block;
        padding: 0 30px;
        &:before,
        &:after {
            content: "";
            position: absolute;
            top: 50%;
            display: inline-block;
            width: 28px;
            height: 1px;
            background-color: $c-black;
        }
        &:before {
            left: 0;
            transform: rotate(60deg);
        }
        &:after {
            transform: rotate(-60deg);
            right: 0;
        }
    }

    &__annotation {
        font-size: 12px;
    }

    &__button {
        display: inline-flex;
        justify-content: center;
        align-items: center;
        font-size: 24px;
        // font-weight: bold;
        color: $c-white;
        background-color: $c-primary;
        border: solid 1px #888;
        border-radius: 8px;
        box-shadow: 2px 2px 0px #ccc;
        padding: 14px 18px;
        min-width: 400px;
        max-width: 100%;
        text-decoration: none;
        position: relative;
        &:before {
            // font-family: "Material Icons";
            // content: "\eaaa";

            font-family: "Font Awesome 5 Free";
            content: "\f0a9";
            font-weight: 900;

            margin-right: 8px;
        }
        &:hover {
            color: $c-white;
            opacity: 0.7;
        }

        &--primary {
            @extend .c-cta__button;
            color: $c-primary;
            background-color: $c-white;
            border: solid 2px $c-primary;
            &:hover {
                color: $c-primary;
                opacity: 0.7;
            }
        }
        &--secondary {
            @extend .c-cta__button;
            color: $c-white;
            background-color: $c-secondary;
            border: solid 2px $c-secondary;
            &:hover {
                color: $c-white;
                opacity: 0.7;
            }
        }
        //
        &--green {
            @extend .c-cta__button;
            color: $c-white;
            background-color: #30bd00;
            border: solid 2px #30bd00;
            &:hover {
                color: $c-white;
                opacity: 0.7;
            }
        }
    }

    @include breakpoint(tablet) {
        &__button {
            font-size: 20px;
            min-width: 320px;
            width: 340px;
        }
    }
    @include breakpoint(sp) {
    }
}
