/* l-header
--------------------------- */
@use "./../foundation/system" as *;

$header-height: 60px;

.l-header {
    font-size: 14px;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 1;
    background-color: $c-white;
    input {
        display: none;
    }
}

.l-header__inner {
    @include content-maxwidth();
    margin: 0 auto;
    padding: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.l-header__logo {
    // flex: 0.7;
    // img {
    //     width: 100%;
    // }
}

// CTAボタン
.l-header__call {
    display: inline-block;
    color: $c-black;
    text-decoration: none;
    line-height: 1.2;
    font-size: 12px;
    span {
        font-size: 28px;
    }
}
.l-header__button {
    line-height: 1.7;
    font-weight: bold;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    color: $c-white;
    background-color: $c-primary;
    border: solid 1px #888;
    border-radius: 8px;
    box-shadow: 2px 2px 0px #ccc;

    transition: 0.4s;
    text-decoration: none;
    padding: 8px 24px;
    &:nth-of-type(2) {
        background-color: #2993ff;
        border-color: #2993ff;
    }
    &:last-of-type {
        background-color: #30bd00;
        border-color: #30bd00;
    }
    &:hover {
        color: $c-white;
        opacity: 0.7;
    }
}

// スマホ用メニュー
.l-header__label {
    height: $header-height;
    line-height: $header-height;

    width: 72px;
    position: absolute;
    right: 0;
    top: 0;
    text-align: center;
    background: $c-primary;
    color: $c-white;

    display: none;
}

.l-gnavi {
    width: 100%;
    background-color: $c-primary;
}
.l-gnavi__list {
    padding: 4px 20px;
    line-height: $header-height;
}
.l-gnavi__list a {
    color: $c-white;
    text-decoration: none;
    display: inline-block;
}

.l-gnavi__icon {
    color: $c-white;
    padding: 0 6px;
    &:before {
        content: "\f107";
    }
}

.l-gnavi__content {
    position: absolute;
    left: 0;
    min-width: 100%;
    white-space: nowrap;
    background-color: $c-white;
    padding: 14px 20px;
    line-height: 2.4;

    display: none;
    a {
        color: $c-black;
        &:hover {
            text-decoration: underline;
        }
    }
    h3,
    h4 {
        font-weight: bold;
    }
    h4 {
        color: $c-primary;
    }
}

.l-gnavi__list input:checked ~ label .l-gnavi__icon {
    transform: rotate(180deg);
}

.l-gnavi__list input:checked ~ .l-gnavi__content {
    display: block;
}

@include breakpoint(pc) {
    // CTAの表示・非表示
    .l-header__cta--pc {
        display: flex;
        gap: 16px;
    }
    .l-header__cta--sp {
        display: none;
    }

    .l-gnavi__lists {
        display: flex;
        justify-content: space-between;
        align-items: center;
        @include content-maxwidth();
        margin: 0 auto;
        position: relative;
    }
    // グローバルメニューの各項目の中央寄せ
    .l-gnavi__list {
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 6px;

        position: relative;
        height: $header-height;
        // line-height: $header-height;

        label {
            color: $c-white;
        }
    }
    .l-gnavi__list--full {
        position: static;
    }
    .l-gnavi__list--categorize {
        margin-left: 152px;
    }

    // ホバー時の挙動
    .l-gnavi__list:hover {
        border-bottom: solid 4px $c-white;
        padding-bottom: 0;
    }
    // .l-gnavi__list:hover .l-gnavi__content {
    //     display: block;
    // }

    // ホバー時表示されるコンテンツ
    .l-gnavi__content {
        top: $header-height;
        border: solid 1px $c-darkGray;
        border-top: none;
    }
    // ホバー時表示されるコンテンツのリスト横並び
    .l-gnavi__categorylists {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
    }
}
@include breakpoint(tablet) {
    // スマホメニューと同じ高さに設定
    .l-header__inner {
        height: $header-height;
    }
    .l-header__logo {
        max-width: 270px;
    }

    // CTAの表示・非表示
    .l-header__cta--pc {
        display: none;
    }
    .l-header__cta--sp {
        display: flex;
        flex-direction: column;
        gap: 16px;
        padding: 16px 20px;
        text-align: center;
        .l-header__button {
            color: $c-white;
            width: 100%;
        }
    }

    // スマホメニューの表示
    .l-header__label {
        display: inline-block;
        padding-top: 16px;
        &:before {
            content: "メニュー";
        }
        & > span,
        & > span:before,
        & > span:after {
            display: block;
            position: absolute;
            height: 4px;
            width: 25px;
            background-color: $c-white;
            border-radius: 1px;
            content: "";
            left: 0;
            right: 0;
            margin: 0 auto;
        }
        & > span {
            top: 32%;
            &:before {
                transform: translateY(-9px);
            }
            &:after {
                transform: translateY(9px);
            }
        }
    }

    // グローバルメニューのCSS
    .l-gnavi {
        background-color: $c-white;
        border-bottom: solid 1px $c-primary;
    }
    // スマホ用メニュー閉時の非表示
    .l-gnavi__lists {
        display: none;
    }
    .l-gnavi__list {
        height: auto;
        width: 100%;
        white-space: nowrap;
        a {
            color: $c-black;
        }
    }
    .l-gnavi__arrow {
        display: inline-block;
        width: 100%;
    }
    .l-gnavi__icon {
        color: $c-black;
    }
    .l-gnavi__content {
        padding: 0 0 0 8px;
    }
    .l-gnavi__categorylists ul {
        padding: 0 0 0 8px;
    }
    .l-gnavi__childlists--wrap {
        display: flex;
        flex-wrap: wrap;
    }
    .l-gnavi__childlists--wrap li {
        width: 50%;
    }

    // レスポンシブ用メニュー checked
    #navi-button:checked ~ .l-header__label:before {
        content: "閉じる";
    }
    #navi-button:checked ~ .l-header__label span {
        transform: rotate(45deg);
        &:before,
        &:after {
            transition: all 0.4s;
            transform: rotate(90deg);
        }
    }

    // レスポンシブ時のメニュー開閉
    #navi-button:checked ~ .l-gnavi {
        position: fixed;
        overflow-y: scroll;
        overflow-x: hidden;
        height: 100%;
    }
    #navi-button:checked ~ .l-gnavi .l-gnavi__lists {
        display: block;
        max-height: inherit;
        overflow-y: visible;
        padding-bottom: 80px;
    }

    .l-gnavi__lists input[type="checkbox"]:checked ~ .l-gnavi__content {
        max-height: inherit;
        overflow-y: visible;
        display: block;
        position: relative;
    }
}
@include breakpoint(sp) {
}

.l-header--checkout {
    position: absolute;
    opacity: 0;
    width: 100vw;
    height: 100%;
    min-height: 100%;
}
